import { Search } from "lucide-react"
import { Input, Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "shared/components"
import { PostsParams } from "features/post"
import { useTagsQuery } from "features/tag"

export function PostFilters({
  params,
  updateParams,
}: {
  params: PostsParams
  updateParams: (params: PostsParams) => void
}) {
  const { search, sortBy, sortOrder, tag: selectedTag } = params
  const { data: tags, isLoading, error } = useTagsQuery() //   

  return (
    <div className="flex gap-4">
      <SearchFilter value={search} onChange={(value) => updateParams({ ...params, search: value })} />
      <TagFilter
        value={selectedTag}
        options={tags || []} //     
        onChange={(value) => updateParams({ ...params, tag: value })}
        isLoading={isLoading} //   
        error={error} //   
      />
      <SortByFilter value={sortBy} onChange={(value) => updateParams({ ...params, sortBy: value })} />
      <SortOrderFilter value={sortOrder} onChange={(value) => updateParams({ ...params, sortOrder: value })} />
    </div>
  )
}

//  
function SearchFilter({ value, onChange }: { value: string; onChange: (value: string) => void }) {
  return (
    <div className="flex-1">
      <div className="relative">
        <Search className="absolute left-2 top-2.5 h-4 w-4 text-muted-foreground" />
        <Input placeholder=" ..." className="pl-8" value={value} onChange={(e) => onChange(e.target.value)} />
      </div>
    </div>
  )
}

function TagFilter({
  value,
  options,
  onChange,
  isLoading,
  error,
}: {
  value: string
  options?: Array<{ url: string; slug: string }>
  onChange: (value: string) => void
  isLoading?: boolean
  error?: unknown
}) {
  if (isLoading) {
    return (
      <Select disabled>
        <SelectTrigger className="w-[180px]">
          <SelectValue placeholder="  ..." />
        </SelectTrigger>
        <SelectContent>
          <SelectItem value="loading"> ...</SelectItem>
        </SelectContent>
      </Select>
    )
  }

  if (error) {
    return (
      <Select disabled>
        <SelectTrigger className="w-[180px]">
          <SelectValue placeholder="  " />
        </SelectTrigger>
        <SelectContent>
          <SelectItem value="error">  </SelectItem>
        </SelectContent>
      </Select>
    )
  }

  return (
    <Select value={value} onValueChange={onChange}>
      <SelectTrigger className="w-[180px]">
        <SelectValue placeholder=" " />
      </SelectTrigger>
      <SelectContent>
        <SelectItem value="all"> </SelectItem>
        {options?.map((tag) => (
          <SelectItem key={tag.url} value={tag.slug}>
            {tag.slug}
          </SelectItem>
        ))}
      </SelectContent>
    </Select>
  )
}

//   
function SortByFilter({ value, onChange }: { value: string; onChange: (value: string) => void }) {
  const sortOptions = [
    { value: "none", label: "" },
    { value: "id", label: "ID" },
    { value: "title", label: "" },
    { value: "reactions", label: "" },
  ]

  return (
    <Select value={value} onValueChange={onChange}>
      <SelectTrigger className="w-[180px]">
        <SelectValue placeholder=" " />
      </SelectTrigger>
      <SelectContent>
        {sortOptions.map((option) => (
          <SelectItem key={option.value} value={option.value}>
            {option.label}
          </SelectItem>
        ))}
      </SelectContent>
    </Select>
  )
}

//   
function SortOrderFilter({ value, onChange }: { value: string; onChange: (value: string) => void }) {
  return (
    <Select value={value} onValueChange={onChange}>
      <SelectTrigger className="w-[180px]">
        <SelectValue placeholder=" " />
      </SelectTrigger>
      <SelectContent>
        <SelectItem value="asc"></SelectItem>
        <SelectItem value="desc"></SelectItem>
      </SelectContent>
    </Select>
  )
}
